	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Linus Torvalds transformed technology twice — first with the Linux kernel, which helps power the Internet, and again with Git, the source code management system used by developers worldwide. In a rare interview with TED Curator Chris Anderson, Torvalds discusses with remarkable openness the personality traits that prompted his unique philosophy of work, engineering and life. \"I am not a visionary, I'm an engineer,\" Torvalds says. \"I'm perfectly happy with all the people who are walking around and just staring at the clouds ... but I'm looking at the ground, and I want to fix the pothole that's right in front of me before I fall in.","meta":{"duration":"PT21M31S"},"presenter":{"name":"Linus Torvalds"},"source":{"id":"o8NPllzkFhE","provider":"youtube"},"slug":"the-mind-behind-linux","tag":["TED Talk","Linux","Git","Interview","open-source"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/o8NPllzkFhE/default.jpg","height":90,"width":120}},"title":"The mind behind Linux"}}]}},"pathContext":{"slug":"ted-talk","tag":"TED Talk"}}

